import React from 'react';
import PeopleTable from './components/PeopleTable';
import './App.css';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      data: null,
      dataFiltered: null,
    };
  }

  componentDidMount() {
    fetch('https://mate-academy.github.io/react_people-table/api/people.json')
      .then(response => response.json())
      .then((data) => {
        const newData = data.map((d, i) => {
          d.id = i + 1;

          return d;
        });

        this.setState({ data: newData, dataFiltered: newData });
      });
  }

  render() {
    if (this.state.data === null) {
      return null;
    }

    return (
      <>
        <h1>People</h1>
        <input onChange={(e) => {
          const search = e.target.value.toLowerCase();
          const Filtered = this.state.data.filter(x => x.name.toLowerCase().indexOf(search) > -1
             || (x.father || '').toLowerCase().indexOf(search) > -1
             || (x.mother || '').toLowerCase().indexOf(search) > -1);

          this.setState({ dataFiltered: Filtered });
        }

        }
        />
        <PeopleTable people={this.state.dataFiltered} />
      </>
    );
  }
}

export default App;
