import React from 'react';
import Person from './Person';

class PeopleTable extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      people : props.people,
      sortDirection: 'asc',
    }
  }
  render(){
    return(
    <div>
      <button onClick = {(e) =>{
        const newDirection =  this.state.sortDirection === 'asc'? 'desc' : 'asc';
          this.setState ({
            sortDirection : newDirection,
            people: this.state.people.sort((a,b)=>{
              return (a.name===b.name) ?
                0
                : a.name > b.name
                ? newDirection === 'asc' ? -1 : 1
                : newDirection === 'asc' ? 1 : -1

            })
          })


      }}>
        Name
      </button>
      <div>
      {this.state.people.map((p) => <Person person={p}/>)}
      </div>

    </div>
)}

}

export default PeopleTable;
